import React from 'react';
// import PageHeader from './PageHeader';
import PageFooter from './PageFooter';
import {Link} from 'react-router-dom';
import logo from "../icon/logo.png";
import Notification from './Notification';



// import undraw_Code_Thinking from "../img/undraw_Code_thinking_re_gka2.svg"
// import undraw_content_team_3epn from  "../img/undraw_content_team_3epn.svg";
// import undraw_teaching_f1cm from "../img/undraw_teaching_f1cm.svg";
// import undraw_Online_learning_re_qw08 from "../img/undraw_Online_learning_re_qw08.svg";
// import  undraw_chore_list_re_2lq8 from "../img/undraw_chore_list_re_2lq8.svg";
// import undraw_authentication_fsn5 from "../img/undraw_authentication_fsn5.svg";
// import undraw_connected_world_wuay from  "../img/undraw_connected_world_wuay.svg";
import undraw_teaching_f1cm from "../img/undraw_teaching_f1cm.svg";

class BecomeInstructorRoute extends React.Component  {
  constructor (props){
    super(props)
    this.state = {
      errorMessage: '',
      fn_err: true,
      ln_err: true,
      em_err: true,
      un_err: true,
      pass_err: true,
      rp_err: true,
      rf_err: false
    }
  }

closeNotificationBox = ()=>{
  let elem  = document.querySelector("section.notification");
  elem.style.display = 'none';
}

 openNotificationBox = (message) => {
    let elem  = document.querySelector("section.notification");
    elem.style.display = "block";
    document.querySelector("#notification-text").innerHTML = message;

}

getFormData =(ev)=>{
  let name = ev.target.name;
  let val = ev.target.value;
  let elem  = document.querySelector("section.notification");

if (name === "firstname"){
  if (/^[a-zA-Z ]*$/.test(val) && val !== ''){
    this.setState({[name]:val});
    this.setState({errorMessage:'satisfied' });
    ev.target.style.borderColor = '#27ae60';
    this.setState({fn_err: false});
  }else {
    this.setState({errorMessage: 'invalid firstname'});
    elem.style.display = 'block';
    ev.target.style.borderColor = '#e91e63';
    this.setState({fn_err: true});
  }
}

if (name === "lastname"){
  if (val !== "" && /^[a-zA-Z ]*$/.test(val) ){
   this.setState({[name]:val});
   this.setState({errorMessage:'satisfied' });
   ev.target.style.borderColor = '#27ae60';
    this.setState({ln_err: false});
 }else {
   this.setState({errorMessage: 'invalid lastname'});
   elem.style.display = 'block';
   ev.target.style.borderColor = '#e91e63';
   this.setState({ln_err: true});
 }

}

if (name === 'email') {
   if ( val !== "" && /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(val)){
   this.setState({[name]:val});
   this.setState({errorMessage:'satisfied' });
   ev.target.style.borderColor = '#27ae60';
   this.setState({em_err: false});

 }else {
   this.setState({errorMessage: 'email is invalid ex: johndoe@mail.com'});
   elem.style.display = 'block';
   ev.target.style.borderColor = '#e91e63';
   this.setState({em_err: true});
 }

}

if (name === 'username') {
   if (val !== "" && /^[0-9a-zA-Z ]*$/.test(val)){
    this.setState({[name]:val});
    this.setState({errorMessage:'satisfied' });
    ev.target.style.borderColor = '#27ae60';
    this.setState({un_err: false});
  }else {
    this.setState({errorMessage: 'username is invalid!'});
    elem.style.display = 'block';
    ev.target.style.borderColor = '#e91e63';
    this.setState({un_err: true});
  }

}

if (name === 'pass') {
  if (val !== "" && /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/.test(val) && val.length >= 8){
      this.setState({[name]:val});
      this.setState({errorMessage:'satisfied' });
      ev.target.style.borderColor = '#27ae60';
      this.setState({pass_err: false});
  }else {
    this.setState({errorMessage: 'Hint: Minimum eight characters, at least one uppercase letter, one lowercase letter, one number and one special character'});
    elem.style.display = 'block';
    ev.target.style.borderColor = '#e91e63';
    this.setState({pass_err: true});
  }


}

if (name === 'rpass'){
    if (val !== "" && this.state.pass === val && /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/.test(val) && val.length >= 8){
      this.setState({[name]:val});
      this.setState({errorMessage:'satisfied' });
      ev.target.style.borderColor = '#27ae60';
      this.setState({rp_err:false});

  }else {
    this.setState({errorMessage: 'password mismatch!'});
    elem.style.display = 'block';
    ev.target.style.borderColor = '#e91e63';
    this.setState({rp_err: true});
    return false;
  }

}

if (name === 'referrer') {
  if (name === "referrer"  && /^[0-9a-zA-Z ]*$/.test(val)){
      this.setState({[name]:val});
      this.setState({errorMessage:'satisfied' });
      ev.target.style.borderColor = '#27ae60';
      this.setState({rf_err: false});

  }else {
    this.setState({errorMessage: 'invalid referrer!'});
    elem.style.display = 'block';
    ev.target.style.borderColor = '#e91e63';
    this.setState({rf_err: true});
  }
}

}

CreateAccount =(ev)=>{
  ev.preventDefault();
  let n  = document.querySelector("section.notification");
  let t = document.querySelector("#notification-text");
  let elem = document.getElementById('load');
  n.style.display = 'block';
  this.setState({errorMessage: ''});

if (this.state.fn_err === true || this.state.fn_err === true || this.state.em_err === true || this.state.un_err === true || this.state.pass_err === true || this.state.rp_err === true || this.state.rf_err === true ) {
  this.setState( {errorMessage: 'data not satisfied!'});
  return false;
}else {
  elem.style.display='block';
  t.style.display = 'none';

  async function postData(url = '', data = {}) {
  // Default options are marked with *
  const response = await fetch(url, {
    method: 'POST', // *GET, POST, PUT, DELETE, etc.
    mode: 'cors', // no-cors, *cors, same-origin
    cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
    credentials: 'same-origin', // include, *same-origin, omit
    headers: {
      'Content-Type': 'application/json'
      // 'Content-Type': 'application/x-www-form-urlencoded',
    },
    redirect: 'follow', // manual, *follow, error
    referrerPolicy: 'no-referrer', // no-referrer, *no-referrer-when-downgrade, origin, origin-when-cross-origin, same-origin, strict-origin, strict-origin-when-cross-origin, unsafe-url
    body: JSON.stringify(data) // body data type must match "Content-Type" header
  });
  return response.json(); // parses JSON response into native JavaScript objects
}

postData('/signup', {
  firstname: this.state.firstname ,
  lastname: this.state.lastname ,
  email: this.state.email,
  username: this.state.username ,
  pass: this.state.pass,
  rpass: this.state.rpass ,
  referrer: this.state.referrer
 })
  .then( (res) => {
    console.log(res);
    elem.style.display='none';
    t.style.display = 'block';
    this.setState({errorMessage: res.errMessage});
    if (res.errStatus !== -1){
      setTimeout(()=>  window.location = '#/signin'  , 3000);
    }
  });

}

}

componentDidMount = ()=>{
  window.scrollTo(0 , 0);

}

  render (){
        return (
            <section className="page-wrapper">
            <Notification errorMessage={this.state.errorMessage}/>

                <div className="title" style={{paddingTop: 50}}>
                    <h3>Create Instructor Account</h3>
                </div>


                <section className="signup">
                  <div className="signup-panel">
                      <div className="signup-svg">
                          <img src={undraw_teaching_f1cm} alt="signup" />
                      </div>


                      <div className="signup-form">
                          <div className="form-logo">
                             <Link to="/"> <img src={logo} alt="logo" /></Link>
                          </div>
                      <form action="#" method="POST" onSubmit={this.CreateAccount}>
                      <h3>fill the form below to continue.</h3>
                      <div className="box-card-info-theme">
                        <p> Features disable, kindly contact us to make enquiry. </p>
                      </div>
                        <div className="form-group">
                             <input onChange={this.getFormData} type="text" placeholder="First Name" name="firstname" />
                        </div>

                        <div className="form-group">
                         <input onChange={this.getFormData} type="text" placeholder="Last Name" name="lastname"/>
                       </div>

                       <div className="form-group">
                         <input onChange={this.getFormData} type="text" placeholder="Email" name="email"/>
                       </div>

                       <div className="form-group">
                         <input  onChange={this.getFormData} type="text" placeholder="Username" name="username"/>
                       </div>

                       <div className="form-group">
                         <input onChange={this.getFormData} type="password" placeholder="Password" name="pass"/>
                       </div>

                       <div className="form-group">
                         <input onChange={this.getFormData}  type="password" placeholder="Repeat Password" name="rpass"/>
                       </div>

                       <div className="form-group">
                         <input onChange={this.getFormData} type="text" placeholder="Referer (Optional)" name="referrer"/>
                       </div>

                       <div className="form-group">
                           <button disabled className="btn" onclick={(event) => this.CreateAccount}> Create Account <span className="fas fa-user-plus"></span> </button>
                       </div>



                       <div className="form-group">
                         <h6 style={{paddingTop: 30}}> Or Create Account With</h6>

                         <div className="party-item">
                           <Link to="#" className="fab fa-facebook"></Link>
                         </div>

                         <div className="party-item">
                           <Link to="#" className="fab fa-instagram"></Link>
                         </div>


                         <div className="party-item">
                           <Link to="#" className="fab fa-google"></Link>
                         </div>
                         <div className="party-item">
                           <Link to="#" className="fab fa-linkedin"></Link>
                         </div>

                       </div>


                    </form>
                      </div>
                  </div>
              </section>


            <PageFooter/>
            </section>
        )

}

}


export default BecomeInstructorRoute;
