import React from 'react';
import PageHeader from '../PageHeader';
// import PageFooter from '../PageFooter';
import {Link} from 'react-router-dom';
// import LoginComponent from '../LoginComponent';
// import OfflineComponent from '../OfflineComponent';
// import SofflineComponent from '../SofflineComponent';
// import SloginComponent from '../SloginComponent';
import person from "../../img/person.png";
import { CopyBlock , CodeBlock  , vs2015} from "react-code-blocks";



class ReactCourseRoute extends React.Component {
  constructor(props){
    super(props)
    this.state = {src:  person ,
      isLogin: false ,
       user_session: this.props.user_session,
       code: {basic: ''}
     }
  }
  closeCourseOutline = ()=> {
    let elem = document.querySelector('div.course-outlines ');
    elem.style.display= 'none';
  }

  componentDidMount = ()=>{
    window.scrollTo(0 , 0)
    let session = this.props.user_session;
    console.log(session)
    if (session !== null){
      let src = (session.profile_pic !== null && session.profile_pic !== undefined) ?  `data:${"png" || "jpg" | "jpeg"};base64,${Buffer.from(session.profile_pic).toString('base64')}` : person;
      this.setState({src: src});
      this.setState({user_session: session});
      this.setState({isLogin: true});
    }else {
        this.setState({isLogin: false});
    }



  }
  render(){
    let basic_code =
`<div id="myReactApp"></div>
<script type="text/babel">
  function Greeter(props) {
    return <h1>{props.greeting}</h1>;
  }
  const App = <Greeter greeting="Hello, World!" />;
  ReactDOM.render(App, document.getElementById('myReactApp'));
</script>`
let basic_code_2 =
`<div id="myReactApp">
  <h1>Hello, World!</h1>
</div>
`

    // let ComtoRender  = (this.state.isLogin) ? <LoginComponent profile_pic={this.state.src} user_session={this.state.user_session}/> : <OfflineComponent/>;
    // let SmallComtoRender = (this.state.isLogin) ? <SloginComponent profile_pic={this.state.src} user_session={this.state.user_session}/> : <SofflineComponent/>;

    return(
      <React.Fragment>
      <PageHeader />
        <section className="page-wrapper">
            <div class="courses-container-parent" style={{height: window.innerHeight}}>
            <div className="course-outlines-c">
                  <div className="open-course-outline"> <i className="fas fa-chevron-right" style={{fontSize: 20 ,  margin: 0}}> </i>  </div>
            </div>

              <div className="course-outlines">
                        <ul style={{padding: 5}} className="course-outline-list">
                          <li> <i to="#" className=" fas fa-times-circle" onClick={this.closeCourseOutline}> </i></li>

                          <li><Link to="#"> ReactJS Overview </Link> </li>
                          <li> <Link to="#">Introduction to ReactJS </Link> </li>
                          <li><Link to="#"> Setting Up Development Environment  </Link></li>
                          <li> <Link to="#">ReactJS Engine  </Link></li>
                          <li> <Link to="#">ReactJS Component  </Link></li>
                          <li><Link to="#">ReactJS JSX  </Link></li>
                          <li> <Link to="#">ReactJS Render </Link> </li>
                          <li> <Link to="#">ReactJS Class  </Link></li>
                          <li> <Link to="#">ReactJS Props  </Link></li>
                          <li><Link to="#"> ReactJS Events  </Link></li>
                          <li> <Link to="#">ReactJS Forms  </Link></li>
                          <li> <Link to="#">ReactJS Router  </Link></li>
                          <li> <Link to="#">ReactJS Styling With CSS  </Link></li>
                        </ul>
              </div>

              <div className="course-main-reader">
                <div className="box-card-theme">
                    <h3 style={{padding: 10}}> ReactJS </h3>
                    <p style={{padding: 10}}>React is a free and open-source front-end JavaScript library for building user interfaces based on UI components. It is maintained by Meta and a community of individual developers and companies. React can be used as a base in the development of single-page or mobile applications.</p>
                </div>

                <div className="box-card-green">
                    <h3 style={{padding: 10}}> Difficulty Level </h3>
                    <p style={{padding: 10}}> Easy </p>
                </div>

                <div className="box-card-plain">
                  <p> The following is a rudimentary example of React usage in HTML with JSX and JavaScript. </p>
                </div>


                <div className="box-card-theme">
                    <h3 style={{padding: 10}}> Basic Usage </h3>
                    <p id="feed-preview-caption">

                        {<CodeBlock
                             text={basic_code}
                             language='javascript'
                             showLineNumbers={false}
                             theme={vs2015}
                             showLineNumbers={true}
                             wrapLines
                           />
                        }
                    </p>
                </div>

                <div className="box-card-plain">
                    <p> The Greeter function is a React component that accepts a property greeting. The variable App is an instance of the Greeter component where the greeting property is set to 'Hello, World!'. The ReactDOM.render method then renders the Greeter component inside the DOM element with id myReactApp.  </p>
                </div>

                <div className="box-card-plain">
                  <p> When displayed in a web browser the result will be:  </p>
                </div>
                {
                  <div className="box-card-theme">
                      <h3 style={{padding: 10}}> Basic Usage </h3>
                      {<CopyBlock
                           text={basic_code_2}
                           language='javascript'
                           showLineNumbers={false}
                           theme={vs2015}
                           showLineNumbers={true}
                           wrapLines
                         />
                      }
                  </div>
                }



              </div>
            </div>
        </section>
    </React.Fragment>);
  }

}


export default ReactCourseRoute;
