import React from 'react';
import Comment from './Comment';

class CommentCard extends React.Component {
  constructor(props){
    super(props);
    this.state = {display: false , comment: ''}
  }
  closeComment = ()=>{
    this.setState({display: !this.props.display});
    let elem = document.getElementById(`${this.props.id}`);
    elem.style.display = 'none';
  }

getComment=(ev)=>{
  if (ev.target.value !== '') {
    this.setState({comment: ev.target.value});
  }
}
addComment = (id , key)=> {
  // alert(this.state.comment);
  document.getElementById('comment').value = '';

  if ( this.props.isLogin !== false ) {
    fetch('/addcomment' , {method: 'POST', headers: {"Content-Type": "application/json"} , body:JSON.stringify({key: key , id: id , comment: this.state.comment, username: this.props.user_session.username , profile: 'logo'})})
    .then(res=> res.json())
    .then(res => console.log(res));
    this.setState({dislike: true});

  }

}
componentDidMount =()=>{
  this.setState({display: this.props.display});
  let elem = document.getElementById(`${this.props.id}`);
  elem.style.display = 'none';
}

  render(){

    return(
      <div id={this.props.id} className="comment-card">
       <i onClick={this.closeComment}  style={{position: 'fixed' , color: 'white' , marginBottom: 15, float: 'right'}} className=" fas fa-times-circle" ></i>
       <div className="addComment">
           <textarea  style={{ margin: 0, marginBottom: 3 }} onChange={this.getComment} name="comment"  id="comment" rows="1" cols="20" placeholder="Leave your caption  here!"></textarea>
           <button  onClick={()=>this.addComment(this.props.id , this.props.ikey)} style={{textAlign:'center' , marginRight: 5}} type="button" name="button" > Add Comment  <i className="fas fa-paper-plane"> </i> </button>
       </div>

         <Comment key={this.props.ikey} ikey={this.props.ikey} id={this.props.id} postHeader={this.props.postHeader} />
       </div>

    )
  }
}


export default CommentCard;
