import React from 'react';
import PageHeader from './PageHeader';
import PageFooter from './PageFooter';
import {Link} from 'react-router-dom';
import SofflineComponent from './SofflineComponent';
import SloginComponent from './SloginComponent';
import CommentCard from './CommentCard';
import loading from "../icon/loading.gif";


// import undraw_Code_Thinking from "../img/undraw_Code_thinking_re_gka2.svg";
// import undraw_teaching_f1cm from "../img/undraw_teaching_f1cm.svg";
// import undraw_Online_learning_re_qw08 from "../img/undraw_Online_learning_re_qw08.svg";
// import  undraw_chore_list_re_2lq8 from "../img/undraw_chore_list_re_2lq8.svg";
import PostReader from './PostReader';


class FeedRoute extends React.Component  {
constructor (props) {
  super(props);

  this.state = {
    blogData: [],
    user_session: null ,
    isLogin: false,
    comment: false,
    component: null
  }
}




componentWillMount = ()=>{
  // let elem = document.querySelector('div.feed-thread');
  // let elemload = document.querySelector('div.feed-thread-loading');
  //
  //
  // elem.style.display='none';
  // elemload.style.display="block";
  // fetch("/get_watchboard" , {method: 'GET' , headers:{"Content-Type":"application/json"}})
  // .then(res => res.json())
  // .then((res)=>{
  //   elem.style.display='block';
  //   elemload.style.display="none";
  //
  //   this.setState({blogData: res});
  // })


}

openComment=(id)=>{
  let elem = document.getElementById(`${id}`);
  elem.style.display = 'flex';
}


componentDidMount = ()=>{
  window.scrollTo(0 , 0);

  let elemft = document.querySelector('div.feed-thread');
  let elemload = document.querySelector('div.feed-thread-loading');


  elemft.style.display='none';
  elemload.style.display="flex";
  fetch("/get_watchboard" , {method: 'GET' , headers:{"Content-Type":"application/json"}})
  .then(res => res.json())
  .then((res)=>{
    elemft.style.display='block';
    elemload.style.display="none";

    this.setState({blogData: res});
  })



  let elem = document.querySelector("a#toggleTheme");
  let DARK_MODE = localStorage.getItem("DARK_MODE");


  if (DARK_MODE === "OFF") {
      elem.className = "fas fa-toggle-off";


  }else if (DARK_MODE === "ON"){
      elem.className = "fas fa-toggle-on";
      localStorage.getItem("DARK_MODE" , "ON");

  }

  let session = JSON.parse(localStorage.getItem('user_session'));
  if (session !== null){
    this.setState({user_session: session});
    this.setState({isLogin: true});
  }else {
      this.setState({isLogin: false});
  }


}


  render (){
      let SmallComtoRender = (this.state.isLogin) ? <SloginComponent profile_pic={this.props.profile_pic} user_session={this.state.user_session}/> : <SofflineComponent/>;

        return (
            <section className="page-wrapper">
            {
              this.state.blogData.map((item)=>{
                return (
                  <div>
                  <CommentCard user_session={this.state.user_session} isLogin={this.state.isLogin} id={item._id} key={item.key} ikey={item.key} postHeader={item.postHeader}/>
                </div>);
              })
            }
            <PageHeader  changeTheme={this.props.changeTheme}/>

            <section className="feed-container">

            <div className="left-feed-panel" >

              {SmallComtoRender}

                <div className="search">
                        <input  type="text" placeholder="search" />
                        <button style={{marginLeft: 5}} className="fas fa-search"></button>
                </div>


                <ul className="left-menu">
                <li><Link to="/feed"><i className="fas fa-rss-square"> </i> News Feed</Link> </li>
                <li><Link to="/post"><i className="fas fa-edit"> </i> Create Post</Link> </li>
                <li><Link to="#"><i className="fas fa-users"></i> Forum</Link> </li>
                <li><Link to="#"><i className="fas fa-tv"> </i> Live </Link>  </li>
                <li><Link to="#"><i className="fas fa-comment"></i> Top Discussion </Link> </li>
                <li><Link to="/settings"><i className="fas fa-cogs"></i> Settings </Link> </li>
                <li><Link to="#"><i className="fas fa-toggle-off"></i> Dark Mode </Link> </li>
                <li><Link to="#"><i className="fas fa-mail-bulk"></i> Subscribe </Link> </li>

                <li><Link to="/signup"><i className="fas fa-user-plus"></i> Create Account </Link> </li>
                <li><Link to="#"><i className="fas fa-sign-out-alt"></i> Logout </Link> </li>

                </ul>
            </div>
            { /*<div className="hider">
              <div className="hider-item">
                <i id ="toggleMenu" onclick="toggleFeedMenu()" className="fas fa-angle-left "></i>
              </div>
            </div> */}
            <section className="notification">
                <div className="notification-box">
                    <span style={{float: 'right'}}  className=" fas fa-times-circle" onClick={this.closeNotificationBox}></span>
                    <p id="notification-text" style={{padding: 10 , textJustify: 'newspaper'}}> .</p>
                </div>
            </section>


        <div className="main-feed-panel">


            <div className="mf-container">
                <div className="feed-thread-loading"><img src={loading} style={{width: window.innerWidth / 3}} alt="loading..."/></div>
                <div className="feed-thread">

                            {
                              this.state.blogData.map((item)=>(
                                  <div>
                                  {/*<CommentCard id={item._id} postHeader={item.postHeader}/> */}
                                  <PostReader key={item._id}  openComment={this.openComment} id={item._id} ikey={item.key} postHeader={item.postHeader} postFooter={item.postFooter} postData={item.postData} />

                                </div>
                              ))
                            }



                        </div>
        </div>
        </div>
        </section>



            <PageFooter/>
            </section>
        )

}

}

export default FeedRoute;
