uploadToServer = ()=>{
  var form = new FormData();
  form.append(this.state.user_session.email, this.state.croppedImage);
  fetch('/profile_upload' , {method: 'POST' , body:form })
  .then(res => res.json())
  .then(r => console.log(r))
  this.closeImgCropper();
  this.props.updateUserSession();

}
uploadPic=()=>{
  let elem = document.querySelector('input#profile');
  elem.click();
  elem.addEventListener('change' , ()=>{
    this.setState( {src: URL.createObjectURL(elem.files[0])});
    if (elem.files[0].type === "image/png" || elem.files[0].type === "image/jpg" || elem.files[0].type === "image/jpeg") {
      this.setState({ppic: URL.createObjectURL(elem.files[0])})
      let cropper = document.querySelector('div.imgCropper')
      cropper.style.display='block';
    }
  });
}
