import React from 'react';
import { CopyBlock  , vs2015} from "react-code-blocks";
import loading from "../icon/loading.gif";

class FeedReader extends React.Component {
  constructor(props){
    super(props);
    this.state = {url: ''}
  }
  componentDidMount =() =>{
    let url = `${this.props.item.imgUrl}`;
    this.setState({src: loading})
    fetch("/img/?src="+url )
    .then((res) => res.json())
    .then(res => {
      this.setState({src:  `data:${this.props.item.type};base64,${Buffer.from(res.buff).toString('base64')}` });
    })

  }
  render (){
    // console.log(this.state.url)
    let display = (this.props.item.src === '' || this.props.item.src === null) ? 'none' : 'block';
    let tdisplay = (this.props.item.caption === '') ? 'none' : 'block';
    // let scr = (this.state.url)


    return(
      <div>
              <div className="feed-card-text">
                  <p id="feed-preview-caption" style={{display: tdisplay}}>
                    {<CopyBlock
                         text={this.props.item.caption}
                         language='javascript'
                         showLineNumbers={false}
                         theme={vs2015}
                         wrapLines
                       />
                    }
                </p>
              </div>

              <div className="feed-card-media">
                  <img src={this.state.src} alt={this.props.item.imgObj} id="imgTag" style={{ display: display, marginBottom: 5, borderWidth: 1, borderStyle: 'solid', borderColor: 'transparent' , borderRadius: 5 }} />
                  <video src="" controls style={{ display: 'none', marginBottom: 5, borderWidth: 1, borderStyle: 'solid', borderColor: 'white' }} id="videoTag" />
              </div>
        </div>
    );
  }
}

export default FeedReader;
