import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import reportWebVitals from './reportWebVitals';
import DataStorage from './enum';




//STEP 1:
//create components using React.lazy
const LightTheme = React.lazy(() => import('./components/LightTheme'));
const DarkTheme = React.lazy(() => import('./components/DarkTheme'));


//STEP 2:
//create a parent component that will load the components conditionally using React.Suspense

const ThemeSelector = ({ children }) => {
  const DARK_MODE = localStorage.getItem('DARK_MODE') || DataStorage.DARK_MODE;
  let component = (DARK_MODE === "OFF" ) ? <LightTheme /> : <DarkTheme />
  return (
    <>
      <React.Suspense fallback={<></>}>
        { /*(DARK_MODE === "OFF") && */ }
        { /*(DARK_MODE === "ON") && */ }
        {component}
      </React.Suspense>
      {children}
    </>
  )
}


ReactDOM.render(
  <ThemeSelector>
    <App />
  </ThemeSelector>,
  document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
