import React from 'react';
// import PageHeader from './PageHeader';
import PageFooter from './PageFooter';

import {Link} from 'react-router-dom';
import logo from "../icon/logo.png";
import Notification from './Notification';

// import undraw_Code_Thinking from "../img/undraw_Code_thinking_re_gka2.svg"
// import undraw_content_team_3epn from  "../img/undraw_content_team_3epn.svg";
// import undraw_teaching_f1cm from "../img/undraw_teaching_f1cm.svg";
// import undraw_Online_learning_re_qw08 from "../img/undraw_Online_learning_re_qw08.svg";
// import  undraw_chore_list_re_2lq8 from "../img/undraw_chore_list_re_2lq8.svg";
import undraw_authentication_fsn5 from "../img/undraw_authentication_fsn5.svg";

class SigninRoute extends React.Component  {
constructor(props){
  super(props);
  this.state = {
    em_err: true,
    pass_err: true,
    errorMessage: '',
    remember: false
  }
}
closeNotificationBox = ()=>{
  let elem  = document.querySelector("section.notification");
  elem.style.display = 'none';
}


getFormData =(ev)=>{

  let name = ev.target.name;
  let val = ev.target.value;
  let elem  = document.querySelector("section.notification");



  if (name === 'email') {
     if (  val !== "" ){
     this.setState({[name]:val});
     // this.setState({errorMessage:'satisfied' });
     ev.target.style.borderColor = '#27ae60';
     this.setState({em_err: false});

   }else {
     this.setState({errorMessage: 'empty field detected!'});
     elem.style.display = 'block';
     ev.target.style.borderColor = '#e91e63';
     this.setState({em_err: true});
   }

  }

  if (name === 'pass') {
    if (val !== ""){
        this.setState({[name]:val});
        // this.setState({errorMessage:'satisfied' });
        ev.target.style.borderColor = '#27ae60';
        this.setState({pass_err: false});
    }else {
      this.setState({errorMessage: 'empty field detected!'});
      elem.style.display = 'block';
      ev.target.style.borderColor = '#e91e63';
      this.setState({pass_err: true});
    }


  }

  if (name === 'remember') {
    this.setState({remember: !this.state.remember});

  }
}
componentDidMount = ()=>{
  window.scrollTo(0 , 0);
}


loginAccount =(ev)=>{
  console.log(this.state.remember);

  ev.preventDefault();
  let n  = document.querySelector("section.notification");
  let t = document.querySelector("#notification-text");
  let elem = document.getElementById('load');
  n.style.display = 'block';
  this.setState({errorMessage: ''});

if (this.state.em_err === true  || this.state.pass_err === true) {
  this.setState( {errorMessage: 'data not satisfied!'});
  return false;
}else {
  elem.style.display='block';
  t.style.display = 'none';

  async function postData(url = '', data = {}) {
  // Default options are marked with *
  const response = await fetch(url, {
    method: 'POST', // *GET, POST, PUT, DELETE, etc.
    mode: 'cors', // no-cors, *cors, same-origin
    cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
    credentials: 'same-origin', // include, *same-origin, omit
    headers: {
      'Content-Type': 'application/json'
      // 'Content-Type': 'application/x-www-form-urlencoded',
    },
    redirect: 'follow', // manual, *follow, error
    referrerPolicy: 'no-referrer', // no-referrer, *no-referrer-when-downgrade, origin, origin-when-cross-origin, same-origin, strict-origin, strict-origin-when-cross-origin, unsafe-url
    body: JSON.stringify(data) // body data type must match "Content-Type" header
  });
  return response.json(); // parses JSON response into native JavaScript objects
}

postData('/signin', {
  email: this.state.email,
  pass: this.state.pass,
  remember: this.state.remember
 })
  .then( (res) => {
    console.log(res);
    elem.style.display='none';
    t.style.display = 'block';
    this.setState({errorMessage: res.errMessage});
    if (res.errStatus !== -1){
      localStorage.setItem('user_session' , JSON.stringify(res.user_session));
      setTimeout(()=>  window.location = '#/'  , 3000);
    }
  });

}

}

  render (){
        return (
          <React.Fragment>
              <Notification errorMessage={this.state.errorMessage}/>


                <div className="title" style={{paddingTop: 50}}>
                  <h3> Login Your Account </h3>
                </div>


                <section className="signup">
                  <div className="signup-panel">
                      <div className="signup-svg">
                          <img src={undraw_authentication_fsn5} alt="signup" />
                      </div>


                      <div className="signup-form">
                          <div className="form-logo">
                             <Link to="/"> <img src={logo} alt="logo" /></Link>
                          </div>
                         <form action="#" method="POST">
                             <div className="form-group">
                                  <input onChange={this.getFormData} type="text" placeholder="Email" name="email"/>
                             </div>

                             <div className="form-group">
                              <input onChange={this.getFormData} type="password" placeholder="Password" name="pass" />
                            </div>


                            <div className="form-group" style={{height: 50}}>
                              <input style={{width: 'auto' , float: 'left'}} onChange={this.getFormData} type="checkbox" name="remember"    />
                              <label style={{float: 'left' , padding: 2,  margin: 0}} htmlFor="remember"> Remember me </label>
                            </div>


                            <div className="form-group">
                                <button className="btn" onClick={this.loginAccount}> Login <span className="fas fa-sign-in-alt"></span> </button>
                            </div>



                            <div className="form-group">
                              <h6 style={{paddingTop: 30}}> Or </h6>

                              <div className="pass-reset">
                               <p>Forget password? <Link to="/resetpassword" >reset password</Link></p>
                               <p>Don't have an account? <Link to="/signup"> Sign up here</Link></p>
                              </div>
                            </div>


                         </form>
                      </div>
                  </div>
              </section>


            <PageFooter/>
            </React.Fragment>

        )

}

}


export default SigninRoute;
