import React from 'react';
import person from "../img/person.png";


class Comment extends React.Component {
  constructor(props){
    super(props);
    this.state = {user_comments: []};
  }

  componentDidMount = ()=>{
    fetch('/getcomment' , {method: 'POST' , headers:{"Content-Type": "application/json"} ,body: JSON.stringify({key: this.props.ikey}) })
    .then(res => res.json())
    .then((comment)=>{
      this.setState({user_comments: comment});
    })
  }

  render(){
    return(
      <>
        {
          this.state.user_comments.map((comment)=>{
          return(
            <React.Fragment>
            <div className="feed-card-topbar">
                <div className="card-profile">
                  <img src={person} alt="@gabriel" />
                </div>
                <div className="card-info"> {comment.commentby} </div>
                <div className="card-info-time" > {comment.date} </div>
            </div>
            <div className="user-comment">
              <p> {comment.comment} </p>
            </div>
            </React.Fragment>
          );
        })}
        </>
    )
  }
}

export default Comment;
