import React from 'react';
import person from "../img/person.png";
import FeedReader from './FeedReader';
// import CommentCard from './CommentCard';



class PostReader extends React.Component {
constructor(props){
  super(props);
  this.state = {like: false , dislike: false}
}



addComment = (d)=>{
  console.log(this.props.id);
}

addLike = (id , key)=>{
  if (this.state.like === false){
    fetch('/like_post' , {method: 'POST', headers: {"Content-Type": "application/json"} , body:JSON.stringify({key: key , id: id})})
    .then(res=> res.json())
    .then(res => console.log(res));
    this.setState({like: true});
  }

}
disLike = (id , key)=>{
  if (this.state.dislike === false){
  fetch('/dislike_post' , {method: 'POST', headers: {"Content-Type": "application/json"} , body:JSON.stringify({key: key , id: id})})
  .then(res=> res.json())
  .then(res => console.log(res));
  this.setState({dislike: true});

}

}
  render (){
    return (
      <div>

      <div className="feed-card">

          <div className="feed-card-topbar">
              <div className="card-profile">
                <img src={person} alt="@gabriel" />
              </div>
              <div className="card-info"> {this.props.postHeader.postby} </div>
              <div className="card-info-time" > {this.props.postHeader.date} </div>
          </div>

      {
        this.props.postData.map((item)=>{
          return(
                <FeedReader key={Math.random()} item={item} />
          )
        })
      }

      <i className="fas fa-chevron-down" style={{float: 'right', color: 'white', cursor: 'pointer'}} onClick={this.toggleCaption}>  </i>
          <div className="circle-parent">
              <div className="circle" onClick={()=>this.addLike(this.props.id , this.props.ikey)}><span className="fas fa-heart"> <sup> {this.props.postFooter.heart} </sup></span> </div>
              <div className="circle" onClick={()=>this.disLike(this.props.id , this.props.ikey)} > <span className="fas fa-heart-broken">  <sup> {this.props.postFooter.heartbreak} </sup></span>  </div>
              <div className="circle" onClick={()=>this.props.openComment(this.props.id)} ><span className="fas fa-comments"><sup> {this.props.postFooter.comments.length} </sup> </span>  </div>
          </div>

    </div>
    </div>

    )
  }
}

export default PostReader;
