import React from 'react';
import load from "../icon/4.svg";



class Notification extends React.Component {
    constructor(props){
      super(props);
      this.state = {errMessage: ''}
    }

    closeNotificationBox = ()=>{
      let elem  = document.querySelector("section.notification");
      elem.style.display = 'none';
    }

    
    render (){
      return(
        <React.Fragment>
        <section className="notification">
            <div className="notification-box">
                <span style={{float: "right"}}  className=" fas fa-times-circle" onClick={this.closeNotificationBox} ></span>
                <p id="notification-text" style={{padding: 10 , textJustify: "newspaper"}}>{this.props.errorMessage}</p>
                <img src={load} alt='load' id='load' />

            </div>
        </section>
        </React.Fragment>
      );
    }
}


export default Notification;
