import React from 'react';
import { CopyBlock  , vs2015} from "react-code-blocks";

class Reader extends React.Component {
  render (){
    let display = (this.props.item.src === '') ? 'none' : 'block';
    let tdisplay = (this.props.item.caption === '') ? 'none' : 'block';
    return(
      <div>
              <div className="feed-card-text">
                  <p id="feed-preview-caption" style={{display: tdisplay}}>
                    {<CopyBlock
                         text={this.props.item.caption}
                         language='javascript'
                         showLineNumbers={false}
                         theme={vs2015}
                         wrapLines
                       />
                    }
                </p>
              </div>

              <div className="feed-card-media">
                  <img src={this.props.item.src} alt={this.props.item.src} id="imgTag" style={{ display: display, marginBottom: 5, borderWidth: 1, borderStyle: 'solid', borderColor: 'transparent' , borderRadius: 5 }} />
                  <video src="" controls style={{ display: 'none', marginBottom: 5, borderWidth: 1, borderStyle: 'solid', borderColor: 'white' }} id="videoTag" />
              </div>
        </div>
    );
  }
}

export default Reader;
