import React from 'react';
// import PageHeader from './PageHeader';
import PageFooter from './PageFooter';
import {Link} from 'react-router-dom';
import logo from "../icon/logo.png";
// import undraw_Code_Thinking from "../img/undraw_Code_thinking_re_gka2.svg"
// import undraw_content_team_3epn from  "../img/undraw_content_team_3epn.svg";
// import undraw_teaching_f1cm from "../img/undraw_teaching_f1cm.svg";
// import undraw_Online_learning_re_qw08 from "../img/undraw_Online_learning_re_qw08.svg";
// import  undraw_chore_list_re_2lq8 from "../img/undraw_chore_list_re_2lq8.svg";
// import undraw_authentication_fsn5 from "../img/undraw_authentication_fsn5.svg";
import undraw_contact_us_15o2 from  "../img/undraw_contact_us_15o2.svg";
class ContactRoute extends React.Component  {

closeNotificationBox = ()=>{}
loginValidator = () =>{}
getCaptionValue = ()=>{}



componentDidMount = ()=>{
  window.scrollTo(0 , 0);
}
  render (){
        return (
            <section className="page-wrapper">
                <section className="notification">
                    <div className="notification-box">
                        <span style={{float: "right"}}  className=" fas fa-times-circle" onClick={this.closeNotificationBox} ></span>
                        <p id="notification-text" style={{padding: 10 , textJustify: "newspaper"}}> .</p>
                    </div>
                </section>

                <div className="title" style={{paddingTop: 50}}>
                  <h3> Contact Us </h3>
                </div>


                <section className="signup">
                  <div className="signup-panel">
                      <div className="signup-svg">
                          <img src={undraw_contact_us_15o2} alt="signup" />
                      </div>


                      <div className="signup-form">
                          <div className="form-logo">
                             <Link to="/"> <img src={logo} alt="logo" /></Link>
                          </div>
                  <form action="#" method="POST">
                      <div className="form-group">
                          <input type="text" placeholder="Username or Email" />
                      </div>

                      <div className="form-group">
                          <textarea cols="50" rows="10" value="Write Message" onChange={this.getCaptionValue}> </textarea>
                      </div>


                      <div className="form-group">
                          <button className="btn" onClick={this.loginValidator}> Send Message <span
                                  className="fas fa-paper-plane"></span> </button>
                      </div>



                      <div className="form-group">
                          <h6 style={{paddingTop: 30}}> Or </h6>


                          <div className="form-group">

                              <div className="party-item">
                                  <Link to="#" className="fab fa-whatsapp"></Link>
                              </div>

                              <div className="party-item">
                                  <Link to="#" href="#" className="fab fa-telegram"></Link>
                              </div>

                              <div className="party-item">
                                  <Link to="#" className="fas fa-envelope-open-text"></Link>
                              </div>

                              <div className="party-item">
                                  <Link to="#" className="fas fa-phone-alt"></Link>
                              </div>

                          </div>

                              </div>
                  </form>
                  </div>
              </div>
              </section>


            <PageFooter/>
            </section>
        )

}

}


export default ContactRoute;
