import React from 'react';
import PageHeader from './PageHeader';
import PageFooter from './PageFooter';
import Notification from './Notification';
import person from "../img/person.png";
import Reader from './Reader';


class PostRoute extends React.Component {
    constructor(props){
      super(props);
      this.state = {
            npost: [],
            postData: [], caption: '' , imgObj: null,
            user_session: null,
            isLogin: false,
            errorMessage: ''

      }
    }

getImageFile = ()=>{
  let input = document.querySelector("#image-cap");
  input.click();

}


pushNewData = (ev)=>{
  ev.preventDefault();
  if ( (this.state.caption !== ''  ||  this.state.imgObj !== null) && (this.state.isLogin === true )) {
    let imgUrl = (this.state.imgObj !== null) ?  URL.createObjectURL (this.state.imgObj) : null;
    let ndata = {imgBuff: (this.state.imgObj !== null) ?   this.state.imgObj : null , caption: this.state.caption , imgUrl: (this.state.imgObj !== null) ? '/post/img/'+this.state.imgObj.size+this.state.imgObj.name : null , src: imgUrl , type: (this.state.imgObj !== null) ? this.state.imgObj.type :  '' };
    let tmpdata =  [...this.state.postData];
    tmpdata.push(ndata);
    this.setState({postData: tmpdata});
  }




  this.setState({caption: ''});
  this.setState({imgObj: null});
  let form = document.forms['postData'];
  form['image'].value = '';
  form['caption'].value = '';
  // console.log(this.state.postData);

}

undowriteToBlog = ()=>{
  this.setState({postData: []})
  localStorage.setItem('blogData' , JSON.stringify([]) );
  window.scrollTo(0 , 0);

}


captionVal = (ev)=>{
   this.setState({caption: ev.target.value});
}

imgVal = (ev)=>{
      this.setState({imgObj: (ev.target.files[0].type === "image/jpeg" || ev.target.files[0].type === "image/png" ) ?  ev.target.files[0] : null});
}



publishPost =()=> {
  let elem  = document.querySelector("section.notification");
  elem.style.display = 'block';
    let tmp = [];
    if (this.state.postData.length > 0 && this.state.isLogin === true ) {
      tmp.push({key: Math.random() , postData: this.state.postData , postHeader: {date: new Date().toUTCString() , postby: this.state.user_session.username} , postFooter: {heart: 0 , heartbreak: 0 , comments: [] } });
      //extract File Objects if presents

      window.cerr = true;
      this.setState({errorMessage: 'publishing your post..'});

        fetch ('/post_watchboard' , {method: 'POST' , headers: {
          // 'Accept': 'application/json, application/xml, text/plain, text/html, *.*',
          'Content-Type': 'application/json'
        }, body: JSON.stringify(tmp)
          })
          .then(res => res.json())
          .then((res)=>{
            this.setState({errorMessage: 'Yeah, there you go!'});

            if (res.errStatus === 0) {
              tmp.map((itm)=>{
                itm.postData.map((data)=>{
                if (data.imgBuff !== null) {
                    var form = new FormData();
                    form.append(data.imgUrl, data.imgBuff);
                    this.setState({errorMessage: 'uploading your files...'});

                    fetch("/post_img_watchboard", {method: "POST", body: form})
                    .then (res => res.json())

                    .then(r => {
                      this.setState({errorMessage: 'Yeah!,There you go!'});

                       window.cerr = (r.errStatus === 0) ? false : true;

                     }
                     )

                  }
                  return(<></>);
                });
                return(<></>);
              });
            }

          })
    }else {
      let elem  = document.querySelector("section.notification");
      elem.style.display = 'block';
      this.setState({errorMessage: 'Error posting to watchboard!'});
      console.log("empty data!");
    }

    this.setState({postData: [] , caption: '' , imgObj: null});
    window.scrollTo(0 , 0);
}

    componentDidMount = () => {
        window.scrollTo(0, 0);

        let session = JSON.parse(localStorage.getItem('user_session'));
        if (session !== null){
          this.setState({user_session: session});
          this.setState({isLogin: true});
        }else {
            this.setState({isLogin: false});
        }



}


    render() {
        return (
            <section className="page-wrapper">

              <Notification errorMessage={this.state.errorMessage}/>
                <PageHeader changeTheme={this.props.changeTheme} />
                <div className="post-edit" style={{marginTop: 50 }}>
                    <div className="preview">
                        <div className="preview-item">
                            <h3>post to watchboard</h3>
                            <div className="feed-card">


                                <div className="feed-card-topbar">
                                    <div className="card-profile">
                                        <img src={person} alt="@gabriel" />
                                    </div>
                                    <div className="card-info"> @gabriel</div>
                                    <div className="card-info-time" > 3 min ago</div>


                                </div>


                                <div id="entry"> </div>
                                {
                                  this.state.postData.map((item)=>{
                                    return(<div> <Reader key={Math.random()} item={item} /> </div>)
                                  })
                                }




                                <div className="comment-box">
                                  <form action="#" name="postData">
                                    <textarea  style={{ margin: 0, marginBottom: 3 }} onChange={this.captionVal} name="caption" rows="4" cols="40" placeholder="Leave your caption  here!"></textarea>

                                    <div style={{display: 'flex' , flexDirection: 'row'}}>
                                        <label htmlFor="image-cap" style={{marginRight: 5}} onClick={this.getImageFile}>
                                            <button type="button" name="button">  <i className="fas fa-image"> </i> </button>
                                        </label>
                                          <input type='file' onChange={this.imgVal} name="image" id='image-cap' style={{display: 'none'}} />
                                          <button type="button" style={{marginRight: 5}}  onClick={this.undowriteToBlog}>  <i className="fas fa-undo"> </i> </button>
                                          <button  style={{marginRight: 5}}  onClick={this.pushNewData}>  <i className="fas fa-eye"> </i> </button>


                                          <button onClick={this.publishPost} style={{marginRight: 5}} type="button" name="button" >  <i className="fas fa-paper-plane"> </i> </button>



                                    </div>
                                    </form>
                                </div>

                                <div className="circle-parent">
                                    <div className="circle" > <span className="fas fa-heart-broken">  <sup> 300 </sup></span>  </div>
                                    <div className="circle" ><span className="fas fa-heart"> <sup> 300 </sup></span> </div>
                                    <div className="circle" ><span className="fas fa-comments"><sup> 300 </sup> </span>  </div>
                                </div>

                            </div>
                        </div>
                    </div>

                </div>

                    <PageFooter />
            </section>
          )

}

}


                export default PostRoute;
