import React from 'react';
import { HashRouter ,  Route, Routes} from 'react-router-dom';
import IndexRoute from './components/IndexRoute';
import SigninRoute from './components/SigninRoute';
import SignupRoute from './components/SignupRoute';
import FeedRoute from './components/FeedRoute';
import SubscribeRoute from './components/SubscribeRoute';
import ContactRoute from './components/ContactRoute';
import ResetPassword from './components/ResetPassword';
import ErrRoute from './components/ErrRoute';
import PostRoute from './components/PostRoute';
import SettingsRoute from './components/SettingsRoute';
import LoadingComponent from './components/LoadingComponent';
import person from "./img/person.png";
import CoursesRoute from './components/CoursesRoute';
import SearchRoute from './components/SearchRoute';

import ReactCourseRoute from './components/courses/ReactCourseRoute';
import JavascriptCourseRoute from './components/courses/JavascriptCourseRoute';
import DeepLearningCourseRoute from './components/courses/DeepLearningCourseRoute';

import BecomeInstructorRoute from './components/BecomeInstructorRoute';
import TutorialRoute from './components/TutorialRoute';


class App extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      darkTheme: false,
      component: null,
      user_session: null,
      isLogin: false,
      src: null
    }
  }



  bannerAnimate = (message) => {
    let interval;

    let indexPageBanner = message;


    let bannerArr  = indexPageBanner.split("");
    let bannerElem = document.querySelector("#page-banner");
     bannerElem.innerHTML = ""


    let i=0;


    let stopBanner = () => {
      clearInterval(interval);
    }

    let appendText = ()=>{
      bannerElem.innerHTML += bannerArr[i];
      if ( i  ===   bannerArr.length -1 ) {
        i = 0;
        stopBanner();

      }else {

        i+=1;

      }
    }


    interval = setInterval(appendText , 150.0);

  }

  changeTheme =()=> {

        let elem = document.querySelector("a#toggleTheme");

        if (this.state.darkTheme === false) {
            elem.className = "fas fa-toggle-off";
            localStorage.setItem("DARK_MODE" , "OFF");



        }else if (this.state.darkTheme === true){
            elem.className = "fas fa-toggle-on";
            localStorage.setItem("DARK_MODE" , "ON");


        }
        this.setState({darkTheme: !this.state.darkTheme});
        setTimeout(()=> window.location.reload() , 2000);

    }




updateUserSession = ()=>{
  fetch('/update_session' , {method: 'POST' , headers: {"Content-Type": "application/json"} , body: JSON.stringify({email: this.state.user_session.email}) })
  .then(r => r.json())
  .then( (res) => {
    localStorage.setItem('user_session' , JSON.stringify(res.user_session));
  })
}
componentWillMount = ()=>{
  document.onreadystatechange = ()=> {
	if (document.readyState !== "complete") {
    this.setState({component: <LoadingComponent/>});
	} else {
    this.setState({component: <MainAppComponent/>});
	}
};


}

  componentDidMount = ()=>{
    window.scrollTo(0 , 0);

    let session = JSON.parse(localStorage.getItem('user_session'));
    if (session !== null){
      let src = (session.profile_pic !== null && session.profile_pic !== undefined) ?  `data:${"png" || "jpg" | "jpeg"};base64,${Buffer.from(session.profile_pic).toString('base64')}` : person;
      this.setState({src: src});
      this.setState({user_session: session});
      this.setState({isLogin: true});



    }else {
        this.setState({isLogin: false});
    }



  }


    render () {
        return (<div>
          {this.state.component}
          </div>);

    }
}

class MainAppComponent extends App{
  render(){
    return(
      <HashRouter>
        <Routes>
          <Route path="/" element={<IndexRoute bannerAnimate={this.bannerAnimate}  changeTheme={this.changeTheme}/>} exact />
          <Route path="/signin" element={<SigninRoute/>} />
          <Route path="/signup" element={<SignupRoute/>} />
          <Route path="/feed" element={<FeedRoute profile_pic={this.state.src} changeTheme={this.changeTheme}/>} />
          <Route path="/subscribe" element={<SubscribeRoute/>} />
          <Route path="/contact" element={<ContactRoute />} />
          <Route path="/resetpassword" element={<ResetPassword/>} />
          <Route path="/post" element={<PostRoute user_session={this.state.user_session} changeTheme={this.changeTheme}/>}  />
          <Route path="/settings" element={<SettingsRoute updateUserSession={this.updateUserSession} user_session={this.state.user_session} profile_pic={this.state.src} changeTheme={this.changeTheme}/>}  />
          <Route path="/courses" element={<CoursesRoute updateUserSession={this.updateUserSession} user_session={this.state.user_session} profile_pic={this.state.src} changeTheme={this.changeTheme}/>}  />
          <Route path="/react" element={<ReactCourseRoute updateUserSession={this.updateUserSession} user_session={this.state.user_session} profile_pic={this.state.src} changeTheme={this.changeTheme}/>}  />
          <Route path="/javascript" element={<JavascriptCourseRoute updateUserSession={this.updateUserSession} user_session={this.state.user_session} profile_pic={this.state.src} changeTheme={this.changeTheme}/>}  />
          <Route path="/deeplearning" element={<DeepLearningCourseRoute updateUserSession={this.updateUserSession} user_session={this.state.user_session} profile_pic={this.state.src} changeTheme={this.changeTheme}/>}  />
          <Route path="/search" element={<SearchRoute updateUserSession={this.updateUserSession} user_session={this.state.user_session} profile_pic={this.state.src} changeTheme={this.changeTheme}/>}  />
          <Route path="/becomeinstructor" element={<BecomeInstructorRoute updateUserSession={this.updateUserSession} user_session={this.state.user_session} profile_pic={this.state.src} changeTheme={this.changeTheme}/>}  />
          <Route path="/tutorials" element={<TutorialRoute updateUserSession={this.updateUserSession} user_session={this.state.user_session} profile_pic={this.state.src} changeTheme={this.changeTheme}/>}  />

          <Route path="*" element={<ErrRoute  bannerAnimate={this.bannerAnimate} changeTheme={this.changeTheme}/>}/>
        </Routes>
      </HashRouter>
    );
  }
}
export default App;
